.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}
.btn--primary {
  background-color: #fff;
  color: #242424;
  border: 1px solid #fff;
}
.btn--outline {
  background-color: transparent;
  color: rgb(255, 255, 255);
  padding: 8px 20px;
  border: 1px solid #fff;
  transition: all 0.3s ease-out;
}
.btn--medium {
  padding: 8px 20px;
  font-size: 20 px;
}
.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}
.btn--medium:hover,
.btn--large:hover {
  background-color: #fff;
  color: #242424;
  transition: all 0.3s ease-out;
}
