.hero-container {
  background: url("https://source.unsplash.com/weekly?water") center
    center/cover no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}
.hero-container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: -100px;
  padding-left: 2rem;
}
.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
}
.hero-btns {
  margin-top: 32px;
}
.hero-btns .btn {
  margin: 6px;
}
@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}
@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }
  .hero-container > p {
    font-size: 30px;
  }
  .btn-mobile {
    display: block;
    text-decoration: none;
  }
  .btn {
    width: 100%;
  }
}
